* {
  box-sizing: border-box;
  padding: 0;
  margin: 0; }

.grid-container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(6, 1fr);
  grid-gap: 2px;
  padding: 10px;
  width: 100vw;
  height: 100vh; }

.header-section {
  grid-column: 1 / 4;
  grid-row: 1; }

.sidebar-section {
  grid-column: 1;
  grid-row: 2 / 7; }

.main-section {
  grid-column: 2 / 4;
  grid-row: 2 / 6; }

.footer-section {
  grid-column: 2 / 4;
  grid-row: 6; }
